.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.inputForm {
  margin: 0% 25% 0% 25%;
  text-align: center;
  /* background-color: #fff; */
}

.weatherCard {
  margin: auto;
  margin-top: 5%;
  text-align: center;
}

.weatherIcon {
  margin: auto;
  width: 25%;
}

.cardBody {
  padding-left: 50%;
}

.logo {
  margin-right: 5%;
}

#root {
  background-image: url(landscape.jpg);
}

.footer {
  margin-top: 15%;
  padding-top: 12.5%;
}

.footer a {
  color: #fff;
}


@media only screen and (max-width: 1000px) {
  .footer {
    padding-top: 122%;
  }
}